'use strict'

/* menu.functions from gent_styleguide 3.0.0alpha12
*  Modified only the exports method */

module.exports = Menu

function Menu (elem) {
  if (typeof gent_styleguide === 'undefined') {
    console.error('You need to include base.js.') // eslint-disable-line
    // no-console
    return
  }

  const openbtn = elem.querySelector('.toggle')
  const closebtn = elem.querySelector('.close')
  const drawer = elem.querySelector('.drawer')
  const overlay = elem.querySelector('.overlay')

  const tabTrap = new gent_styleguide.TabTrap(drawer) // eslint-disable-line no-undef

  /**
   * Closes the hamburger menu
   *
   * @param {event} e onclick or keydown:escape
   */
  const close = (e) => {
    if (e) {
      e.preventDefault()
    }

    drawer.classList.remove('js-opened')
    overlay.classList.remove('js-opened')
    document.removeEventListener('keydown', handleKeyboardInput)

    // return focus to the trigger
    if (openbtn) {
      openbtn.focus()
      openbtn.setAttribute('aria-expanded', false)
    }

    // eslint-disable-next-line no-undef
    gent_styleguide.toggleScrollLockParent(true, drawer)

    // remove the menu from the tabindex
    setTimeout(function () {
      drawer.style.display = 'none'
    }, 500)
  }

  /**
   * Opens the hamburger menu
   *
   * @param {event} e onclick
   */
  const open = (e) => {
    if (e) {
      e.preventDefault()
    }

    // add the menu to the tabindex
    drawer.style.display = 'block'

    setTimeout(function () {
      drawer.classList.add('js-opened')
      overlay.classList.add('js-opened')
    })

    // remember the trigger
    // trigger = e.target;
    openbtn.setAttribute('aria-expanded', true)

    // set focus to the menu
    drawer.focus()

    // handle keyboard input
    document.addEventListener('keydown', handleKeyboardInput)

    // eslint-disable-next-line no-undef
    gent_styleguide.toggleScrollLockParent(false, drawer)
  }

  /**
   * Handle keyboard input
   *
   * @param {object} e event
   */
  const handleKeyboardInput = (e) => {

    if (!tabTrap || !tabTrap.hasFocusables || !e) {
      return
    }

    var keyCode = e.keyCode || e.which

    switch (keyCode) {
      case 9: // tab
        if (e.shiftKey) {
          tabTrap.back(e)
        }
        else {
          tabTrap.next(e)
        }
        break
      case 40: // arrow down
      case 39: // arrow right
        tabTrap.next(e)
        break
      case 38: // arrow up
      case 37: // arrow left
        tabTrap.back(e)
        break
      case 36: // home
        e.preventDefault()
        tabTrap.home()
        break
      case 35: // end
        e.preventDefault()
        tabTrap.end()
        break
      case 27: // esc
        close(e)
        break
    }
  }

  /**
   * Add event listeners.
   */
  openbtn.addEventListener('click', open)
  closebtn.addEventListener('click', close)
  overlay.addEventListener('click', close)

  return {
    open, close
  }
}